import {createSelector} from "reselect";

const getNewsSelector = (state) => {
    return state.newsPage.news;
}

export const getNews = createSelector(getNewsSelector,
    (news) => {
        return news.filter(n => true);
    })

export const getNewsPageSize = (state) => {
    return state.newsPage.pageSize;
}

export const getNewsPage = (state) => {
    return state.newsPage.page;
}

export const getTotalNewsResults = (state) => {
    return state.newsPage.totalResults;
}





