import {newsAPI} from "../api/api";

const SET_NEWS = 'SET_NEWS';
const SET_NEWS_PAGE = 'SET_NEWS_PAGE';
const SET_TOTAL_NEWS_RESULT = 'SET_TOTAL_NEWS_RESULT';

let initialState = {
    news: [],
    pageSize: 20,
    page: 1,
};

const newsReducer = (state = initialState, action) => {
    switch (action.type) {
        case SET_NEWS:
            return {...state, news: action.news}
        case SET_NEWS_PAGE: {
            return {...state, page: action.page}
        }
        default:
            return state;
    }
}

export const setNews = (news) => ({type: SET_NEWS, news})
export const setNewsPage = (page) => ({type: SET_NEWS_PAGE, page})
/*
export const setTotalNewsResult = (totalResults) => ({type: SET_TOTAL_NEWS_RESULT, count: totalResults})
*/


export const requestNews = (page, pageSize) => {
    return async (dispatch) => {
        dispatch(setNewsPage(page));

        let data = await newsAPI.getNews(page, pageSize);
        dispatch(setNews(data.articles));
        // dispatch(setTotalNewsResult(data.totalResults));
    }
}


export default newsReducer;