import React from 'react';
import NewsPage from "./NewsPage";
import PaginatorNews from "./PaginatorNews";

let News = ({page, pageSize, totalResults, news, onPageChanged, ...props}) => {
    return <div>
        <PaginatorNews page={page} pageSize={pageSize} totalNewsCount={totalResults}
                       onPageChanged={onPageChanged}/>
        <div>
            {
                news.map(n => <NewsPage news={n}
                    />
                )
            }
        </div>
        </div>
}

export default News;