import React from 'react';
import s from './News.module.css'

let NewsPage = ({news}) => {
    return (
        <div className={s.newsPageBlock}>
             <span >
                 <div>{news.author}</div>
             </span>
            <div>
                {news.title}
            </div>
            <div className={s.newsPageBlock}>

               <a href={news.url}>Read more</a>

            </div>
          {/*  <div>
                {news.description}
            </div>*/}
           { <div className={s.newsImgBlock}><img src={news.urlToImage}/></div> }

        </div>)
}

export default NewsPage;