import React from 'react';
import News from "./News";
import {connect} from "react-redux";
import {compose} from "redux";
import {requestNews, setNewsPage} from "../../redux/news-reducer";
import {getNews, getNewsPage, getNewsPageSize, getTotalNewsResults} from "../../redux/news-selectors";

class NewsContainer extends React.Component {
    componentDidMount() {
      const {page,pageSize} = this.props;
        this.props.getNews(page,pageSize);
    }

    onPageChanged = (page) => {
        const {pageSize} = this.props;
        this.props.getNews(page, pageSize);

    }


    render() {

        return <>
            <News page={this.props.page}
                  pageSize={this.props.pageSize}
                  news={this.props.news}
                  /*totalResults={this.props.totalResults}*/
                  onPageChanged={this.onPageChanged}
            />
        </>
    }
}

let mapStateToProps = (state) => {
    return {
        /*dialogsPage: state.dialogsPage*/
        news: getNews(state),
        pageSize: getNewsPageSize(state),
        page: getNewsPage(state),
        // totalResults: getTotalNewsResults(state)
    }
}

/*let mapDispatchToProps = (dispatch) => {
    return {
        sendMessage: (newMessageBody) => {
            dispatch(setNewsArticles(newMessageBody));
        }
    }
}*/

export default compose(
    connect(mapStateToProps, {requestNews, setNewsPage, getNews: requestNews})
)(NewsContainer);
